import request from "./request";

/**
 * 
 */
export function applyToEdit(tgid, tid, mode = "extend") {
  if (!tgid || !tid) return Promise.reject("");

  return request({
    method: "PUT",
    url: `/templates/${tgid}/${tid}?mode=${mode}`
  });
}

/**
 * 
 * @param {{
    name: "",
    group: "",
    info: "",
    updateInfo: ""
  }} data
 */
export function updateTemp(data, oldTgid, tid) {
  let bean = {
    name: "",
    group: "",
    info: "",
    updateInfo: ""
  };

  if (!data.group) return Promise.reject("");

  return request({
    method: "PATCH",
    url: `/templates/${oldTgid}/${tid}`,
    data: Object.assign({}, bean, data)
  });
}

/**
 * 
 */
export function listTemplateLibs() {
  return request({
    method: "GET",
    url: "/templates"
  });
}

/**
 * 
 */
export function listTemps(tgid) {
  return request({
    method: "GET",
    url: `/templates/${tgid}`
  });
}

/**
 * 
 */
export function searchTempByName(name) {
  if (!name) return Promise.reject("");

  return request({
    method: "GET",
    url: `/templates`,
    params: {
      name
    }
  });
}

/**
 * 
 */
export function deleteTemp(tgid, tid) {
  if (!tgid || !tid) return Promise.reject("");

  return request({
    method: "DELETE",
    url: `/templates/${tgid}/${tid}`
  });
}

/**
 * 
 */
export function deleteTempLib(tgid) {
  if (!tgid) return Promise.reject("");

  return request({
    method: "DELETE",
    url: `/templates/${tgid}`
  });
}

/**
 * 
 * @param {{
    "category": "",
    "permission": "extend",
    "info": "~"
  }} data
 */
export function addTempLib(data) {
  if (!data) return Promise.reject("");

  return request({
    method: "POST",
    url: "/templates",
    data
  });
}

/**
 * 
 */
export function uploadTemp(file, tgid = "empty") {
  return request({
    method: `/templates/${tgid}`,
    url: `/templates/${tgid}`,
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data: file
  });
}

/**
 * 
 * @param {{
    "list": [
      {
        tid:"5ea83e37e2968353963c3ba1",
        info: ""
      }
    ],
    "info": " XXX"
  }} data
 * @param {String} tgid
 */
export function addTemp(data, tgid) {
  if (!data.list || data.list.length == 0) return Promise.reject("");

  return request({
    method: "POST",
    url: `/templates/${tgid}`,
    data
  });
}

/**
 * 
 */
export function getTempInfo(tgid, tid) {
  return request({
    method: "GET",
    url: `/templates/${tgid}/${tid}`
  });
}

/**
 * 
 */
export function addToFavorites(uid, tid) {
  return request({
    method: "POST",
    url: `/users/${uid}/template/mark`,
    data: {
      tid
    }
  });
}

/**
 * 
 */
export function removeFromFavorites(uid, tid) {
  return request({
    method: "DELETE",
    url: `/users/${uid}/template/mark/${tid}`
  });
}

/**
 * id
 */
export function getTemplatesByIds(data) {
  return request({
    method: "POST",
    url: `/templates/list`,
    data
  });
}


