<template>
  <div>
    <div class="title-line"></div>
    <el-form label-width="80px" class="form">
      <el-form-item label="" required>
        <el-input style="width: 200px" v-model="form.category" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.info" type="textarea" :rows="2" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-upload
          ref="upload"
          multiple
          accept=".doc, .docx"
          :on-exceed="onExceed"
          :limit="7"
          :headers="headers"
          action="http://legal.tgozzz.cn/templates/empty"
        >
          <el-button size="mini" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">doc/docx</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="handleSubmit"></el-button>
        <el-button size="small" @click="handleReset"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { addTempLib, addTemp } from '@/action/template'
export default {
  name: 'addTemplateLib',
  data() {
    return {
      headers: {
        Authorization: this.$store.getters.token
      },
      form: {
        category: '',
        permission: 'extend',
        info: ''
      }
    }
  },
  methods: {
    // 
    onExceed() {
      this.$message.warning(' 7 ')
    },
    // 
    handleReset() {
      this.form.category = ''
      this.form.info = ''
      this.$refs.upload.clearFiles()
    },
    // 
    async handleSubmit() {
      let lib = await addTempLib(this.form)
      let temps = {
        list: this.$refs.upload.uploadFiles.map(e => ({
          tid: e.response.tid,
          info: ''
        })),
        info: ''
      }
      if (temps.list.length != 0) await addTemp(temps, lib.tgid)
      await this.$confirm('', '', {
        confirmButtonText: '',
        type: 'success',
        showCancelButton: false
      })
      this.$router.push({
        path: '/template/library'
      })
    }
  }
}
</script>

<style scoped>
.title-line {
  font-size: 24px;
  height: 60px;
  line-height: 60px;
  margin: 0 20px 20px;
}
.form {
  width: 70%;
}
</style>



