import request from "./request";
import { BASE_URL } from "./request/config";

/**
 * 
 */
export function listContract(pid = "") {
  return request({
    method: "GET",
    url: `/project/${pid}/contracts`
  });
}

/**
 * 
 * 
 * uri
 * 
 * 
 * 
 */
export function uploadContract(pid = "") {
  return `${BASE_URL}/project/${pid}/contracts`;
}

/**
 * 
 */
export function uploadTempContract() {
  return `${BASE_URL}/project/temp/contracts`;
}

/**
 * copy
 * +1
 * 
 * 
 * 
 * 
 * 
 */
export function createContract(pid = "", query = { template: "" }) {
  return request({
    method: "POST",
    url: `/project/${pid}/contracts?template=${query.template}`
    // params: query
  });
}

/**
 * 
 * 
 * 
 * 
 * 
 * handler
 * status
 */
export function confirmAddContract(
  pid = "",
  query = { contract: "" },
  data = { name: "", type: "", tag: "", project: "", desc: "" }
) {
  return request({
    method: "PATCH",
    url: `/project/${pid}/contracts`,
    params: query,
    data
  });
}

/**
 * 
 * 
 * 
 * 
 */
export function rollbackContract(
  pid = "",
  query = { rollback2: "", lastCid: "" }
) {
  return request({
    method: "PUT",
    url: `/project/${pid}/contracts`,
    params: query
  });
}

