/**
 * word
 */
export function download(cid, name) {
  const url = `http://legal.tgozzz.cn/office/downloadWithName?fileId=${cid}&fileName=${name}`;
  var a = document.createElement("a");
  a.href = url;
  a.download = name;
  a.click();
}

/**
 * word
 */
export function downloadWithSignature(pid, cid, name, type, token) {
  const url = `http://legal.tgozzz.cn/project/${pid}/contracts/${cid}/download?filename=temp.${type}&token=${token}`;
  var a = document.createElement("a");
  a.href = url;
  a.download = name + "." + type;
  a.click();
}

/**
 *  id
 */
export function verifySignature(cid) {
  return request({
    method: "GET",
    url: `/project/temp/contracts/${cid}/verify`
  });
}

/**
 * 
 * 
 */
export function rename(pid = "", cid = "", data = { name: "" }) {
  return request({
    method: "PUT",
    url: `/project/${pid}/contracts/${cid}/baseinfo/name`,
    data
  });
}



