import request from "./request";

/**
 * 
 * ```
 * {
 *  "status": "",
 *  "baseInfo": { // 
 *      "name": "",
 *      "tag": "",
 *      "partner": "",
 *      "desc": ""
 *  },
 *  "director": { // 
 *      "uid": "id",
 *      "name": "",
 *      "phone": ""
 *  },
 *  "drafter": { // 
 *      "did": "id",
 *      "name": "",
 *      "phone": "",
 *      "responsibility": ""
 *  },
 *  "auditor": [{ // 
 *      "did": "id",
 *      "order": "",
 *      "name": "",
 *      "phone": "",
 *      "responsibility": ""
 *  }]
 *}
 * ```
 * @param {} data 
 */
export function addProject(data) {
  return request({
    method: "POST",
    url: "/project",
    data
  });
}

/**
 * 
 * ```
 * return {{
    creator: [],
  	director: [],
    drafter: [],
    auditor: []
  }} //
  ```
 */
export function listMyProject() {
  return request({
    method: "GET",
    url: "/project"
  });
}

/**
 * 
 * @param {string} pid id
 * @param {{
    "name": "",
    "tag": "",
    "partner": "",
    "desc": ""
  }} data 
 */
export function updateProject(pid, data) {
  return request({
    method: "PUT",
    url: `/project/${pid}`,
    data
  });
}

/**
 * 
 * @param {string} pid id
 */
export function deleteProject(pid) {
  return request({
    method: "DELETE",
    url: `/project/${pid}`
  });
}

/**
 * 
 * @param {string} pid id
 */
export function getProjectInfo(pid) {
  return request({
    method: "GET",
    url: `/project/${pid}`
  });
}

/**
 * 
 * @param {string} pid id
 */
export function stopProject(pid) {
  return request({
    method: "PUT",
    url: `/project/${pid}/status/stop`
  });
}

/**
 * 
 * @param {string} pid id
 */
export function restartProject(pid) {
  return request({
    method: "PUT",
    url: `/project/${pid}/status/restart`
  });
}



