import request from "./request";

/**
 * 
 */
export function getNotice(nid) {
  return request({
    method: 'GET',
    url: `/notice/${nid}`
  })
}

/**
 * 
 */
export function listNotice(nids) {
  return request({
    method: 'POST',
    url: '/notice/list',
    data: nids
  })
}



